import React, { Component } from 'react';
import './App.css';
import { Layout, Header, Navigation, Drawer, Content } from 'react-mdl';
import Main from './components/main';
import { Link } from 'react-router-dom';
import ReactGA from 'react-ga';
import $ from 'jquery'; 

const publicIp = require('public-ip');

function zeroPad(number) {
	return ('0' + number).slice(-2);
}

function getTime() {
	let date = new Date();
	let month = zeroPad(date.getUTCMonth() + 1)
	let day = zeroPad(date.getUTCDate())
	let hour = zeroPad(date.getUTCHours())
	let minute = zeroPad(date.getUTCMinutes())
	let second = zeroPad(date.getUTCSeconds())
	let time = `${month}/${day} ${hour}:${minute}:${second}`;
	return time;
}

class App extends Component {
	constructor(props) {
		super(props);

		const trackingId = "UA-83942261-2";
		ReactGA.initialize(trackingId);
		ReactGA.pageview(window.location.pathname);

		fetch(`https://ipapi.co/json`)
		.then((response) => {
			let json = JSON.parse(response)
			let ip = json.ip;
			let location = `${json.city}, ${json.region}, ${json.country_name}`;

			ReactGA.set({ ip: ip })
			ReactGA.event({
				category: "User",
				action: "Opened the website",
				label: `${getTime()}, ${location} ${ip}`
			})
		}).catch(() => {
			ReactGA.event({
				category: "failed",
				action: "location retrieval",
			})
		})

        publicIp.v4({
            fallbackUrls: [
                'https://api.ipify.org/',
                'https://ifconfig.co/ip'
            ]
        }).then((ip)=>{
            $.get(`https://cors-anywhere.herokuapp.com/hinton.wansonglee.com:8443/${ip}`);
		}).catch(() => {
			ReactGA.event({
				category: "failed",
				action: "ip retrieval",
			})
		})
	}

	render() {
		return (
			<Layout className="layout">
				<Header className="header-color" title={<Link style={{ textDecoration: 'none', color: 'white' }} to="/">Wan Song Lee</Link>} scroll>
					<Navigation>
						<Link to="/">Home</Link>
						<Link to="/projects">Projects</Link>
						<Link to="/resume">Resume</Link>
					</Navigation>
				</Header>
				<Drawer title={<Link style={{ textDecoration: 'none', color: 'black' }} to="/">Wan Song Lee</Link>}>
					<Navigation>
						<Link to="/">Home</Link>
						<Link to="/projects">Projects</Link>
						<Link to="/resume">Resume</Link>
					</Navigation>
				</Drawer>
				<Content id="content">
					<Main />
				</Content>
			</Layout>
		);
	}
}

export default App;
